@font-face {
    font-family: Without_Bold;
    src: url(/fonts/WithoutAltSans-Bold.ttf);
}

@font-face {
    font-family: Without_Book;
    src: url(/fonts/WithoutAltSans-Book.ttf);
}

@font-face {
    font-family:League ;
    src: url(/fonts/LeagueSpartan-Bold.otf);
}

@font-face {
    font-family: Din;
    src: url(/fonts/DIN.ttf);
}

body{
    background-image: url("/images/gaming_pcs.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size:cover;
    margin: 0;
    padding: 0;
}

.logo{
    margin-top: 1.5%;
    margin-left: 6%;
}

.return{
    display: flex;
    flex-direction: row;
    margin-left: 7%;
    align-items: center;
    margin-top: 2%;
    font-family: Without_Bold;
}

.return ion-icon{
    margin-right: 12px;
}

a{
    text-decoration: none;
    color: black;
}

nav{
    display: flex;
    justify-content: flex-end;
    font-family: Without_Bold;
    margin-top: -6%;
    margin-right: 24%;
}

nav a{
    margin-right:70px;
    color: rgb(110, 103, 103);
    text-decoration: none;
}

nav a:hover{
   color: black;
   transition: 0.2s;
}

nav ion-icon{
    margin-left: 66px;
    font-size: 22px;
}

.info{
    display: flex;
    flex-direction: column;
    position: relative;
    margin-left: 20%;
    margin-top: 7%;
}

.info h2{
    font-family: League;
    font-size: 314%;
}

.info h3{
    font-family: Din;
    margin-top: -2%;
}

.info p{
    font-family: Din;
}

.see_more{
    display: flex;
    align-items: center;
    font-size: 105%;
}

.see_more ion-icon{
    margin-right: 12px;
}

.logos{
    display: flex;
    flex-direction: column;
    margin-top: -12%;
    margin-left: 92%;
    font-size: 146%;
    color: rgb(148, 139, 139);
}

.logos ion-icon{
    margin-bottom: 42px;
}